import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Conhead from '@/components/Conhead'
import Confoot from '@/components/Confoot'
import Paymethod from '@/components/Paymethod'
import Fp1 from '@/components/Fp1'
import Fp2 from '@/components/Fp2'
import Fp3 from '@/components/Fp3'
import Hkinf from '@/components/Hkinf'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
        redirect:'/conhead'
    },
      {
        path:'/conhead',
          name:'Conhead',
          component:Conhead,
          redirect:'/confoot',
          children:[
              {
                path:'/confoot',
                  name:'Confoot',
                  component:Confoot,
                  redirect:'/paymethod',
                  children:[
                      {
                          path:'/paymethod',
                          name:'Paymethod',
                          component:Paymethod
                      },
                      {
                          path:'/fp1',
                          name:'Fp1',
                          component:Fp1
                      },
                      {
                          path:'/fp2',
                          name:'Fp2',
                          component:Fp2
                      },
                      {
                          path:'/fp3',
                          name:'Fp3',
                          component:Fp3
                      },
                      {
                          path:'/hkinf',
                          name:'Hkinf',
                          component:Hkinf
                      }
                  ]
              },
          ]
      },

  ]
})



