<template>
    <div class="fp1">
        <form action="" method="">
            <h3></h3>
            <div class="invoiceType">
                <div class="itTitle"></div>
                <div class="singleBtn">
                    <span></span>
                    <el-radio-group v-model="radio2">
                        <el-radio :label="''"></el-radio>
                        <el-radio :label="''"></el-radio>
                    </el-radio-group>
                </div>
            </div>
            <div class="invoiceInformation">
                <div class="iiTitle"></div>
                <ul class="inforBox">
                    <li>
                        <span></span><i>*</i><input v-model="personName" class="personName" type="text" placeholder="">
                        <div class="warning" style="width:100%"></div>
                    </li>
                    <li>
                        <span></span><input v-model="allPrice" type="text" placeholder="">
                    </li>
                </ul>
                <button type="submit" @click="tijiao"></button>
            </div>
        </form>
    </div>
</template>

<script>
    export default {
        name: "Fp1",
        data () {
            return {
                radio2: '',
                allPrice:'1309.20',
                personName:'',
                arr:""
            };
        },
        created () {
            this.$http.get('/api/product').then(res=>{
                this.arr=res.body;
                console.log(res.body)
            })
        },
        watch:{
          radio2(n,o){
              if(n == ''){
                  this.$router.push({name:'Fp2'})
              }
          }
        },
        methods:{
            tijiao(e){
                var inp=document.querySelector('.personName');
                var warning=document.querySelector('.warning')
                if(!inp.value){
                    e.preventDefault();
                    warning.classList.add('out')
                    setTimeout(function () {
                        warning.classList.remove('out')
                    },2000)

                }
            }
        },

    }
</script>

<style scoped lang="scss">
    .fp1{
        background: white;
        width:1200px;
        margin:37px auto 130px;
        form{
            width:100%;
            h3{
                width:100%;
                height:101px;
                box-sizing: border-box;
                border-bottom: 2px solid #e5e5e5;
                font-size: 20px;
                text-align: center;
                line-height: 120px;
            }
            .invoiceType{
                height:200px;
                width:100%;
                box-sizing: border-box;
                border-bottom: 2px solid #e5e5e5;
                .itTitle{
                    margin:56px 0 0 40px;
                    font-size: 16px;
                }
                .singleBtn{
                    margin:47px 0 0 97px;
                    span{
                        font-size: 14px;
                        color:#999;
                        margin-right: 25px;
                    }
                }
            }
            .invoiceInformation{
                height:440px;
                .iiTitle{
                    margin:56px 0 0 40px;
                    font-size: 16px;
                }
                .inforBox{
                    margin:35px 0 0 65px;
                    li{
                        margin-bottom: 30px;
                        span{
                            font-size: 15px;
                            color:#999;
                        }
                        i{
                            color:red;
                            font-style: normal;
                        }
                        input{
                            width:930px;
                            height:40px;
                            background: #f4f4f4;
                            border-radius: 5px;
                            border:none;
                            box-sizing: border-box;
                            padding-left: 19px;
                            margin-left: 20px;
                        }
                        input::-webkit-input-placeholder{
                            color:#ddd;
                        }
                        input::-moz-placeholder{   /* Mozilla Firefox 19+ */
                            color:#ddd;
                        }
                        input:-moz-placeholder{    /* Mozilla Firefox 4 to 18 */
                            color:#ddd;
                        }
                        input:-ms-input-placeholder{  /* Internet Explorer 10-11 */
                            color:#ddd;
                        }
                    }
                    li:last-child{
                        input{
                            background: none;
                            border:none;
                            font-size: 11px;
                        }
                    }
                }
                button{
                    width:135px;
                    height:49px;
                    border:none;
                    background: #1e9fff;
                    border-radius: 3px;
                    color:white;
                    font-size: 14px;
                    margin:60px auto 0;
                    display: block;
                }
            }
        }
    }
    .warning,.warnings{
        color:red;
        padding-left: 90px;
        box-sizing: border-box;
        font-size: 9px;
        padding-top: 3px;
        opacity: 0;
    }
    .out{
        opacity: 1;
    }
</style>


