<template>
    <div class="fp1">
        <form action="" method="get">
            <div class="title">
                <span><span>(,,)</span></span>
            </div>
            <ul class="information">
                <li>
                    <span></span><i>*</i><input type="text" v-model="tel" placeholder="">
                    <div class="warning"></div>
                </li>
                <li>
                    <span></span><i>*</i><input type="text" v-model="email" placeholder="">
                    <div class="warning"></div>
                </li>
                <li>
                    <span></span><i>*</i><input type="text" v-model="price" placeholder=",:4800.28">
                    <div class="warning"></div>
                </li>
                <li>
                    <span></span><i>*</i><input type="text" v-model="name" placeholder="">
                    <div class="warning"></div>
                </li>
                <li class="hkdate">
                    <span></span><i>*</i>
                    <div>
                        <p class="iconfont">&#xe600;</p>
                        <input class="block" v-model="value1.toString().split('00:0')[0].slice(3)" placeholder="">
                        <el-date-picker
                                v-model="value1"
                                type="date"
                                class="choes"
                                placeholder="">
                        </el-date-picker>
                    </div>
                    <div class="warning" style="width:100%"></div>
                </li>
                <li class="bankUser">
                    <span></span><i>*</i>
                    <select name="" id="" required v-model="bank">
                        <option value="" disabled selected hidden></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                    <div class="warnings"></div>
                </li>
                <li>
                    <span></span><textarea v-model="use" name="" id="" cols="30" rows="10" placeholder=",:"></textarea>
                </li>
            </ul>
            <button type="submit" @click="tijiao"></button>
        </form>
    </div>
</template>

<script>
    export default {
        name: "Hkinf",
        data () {
            return {
                pickerOptions1: {
                    disabledDate(time) {
                        return time.getTime() > Date.now();
                    },
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                },
                value1: '',  //
                tel:'',
                email:'',
                price:'',    //
                name:'',
                bank:'',
                use:'',      //
            };
        },
        methods:{
            tijiao(e){
                // 
                var inputs=document.querySelectorAll('input');
                var sele=document.querySelector('select');
                // 
                var warnings=document.querySelectorAll('.warning')
                var warningss=document.querySelector('.warnings')
                inputs.forEach(function (val,ind) {
                    if(!val.value){
                        if (ind == 5){
                            e.preventDefault();
                            return;
                        }
                        warnings[ind].classList.add('out');
                        setTimeout(function () {
                            warnings[ind].classList.remove('out')
                        },2000)
                        e.preventDefault();
                    }
                })
                if(!sele.value){
                    warningss.classList.add('out')
                    setTimeout(function () {
                        warningss.classList.remove('out')
                    },2000)
                    e.preventDefault();
                }
            }
        },
    }
</script>

<style scoped lang="scss">
    .fp1{
        background: white;
        width:1200px;
        margin:37px auto 130px;
        form{
            width:100%;
            overflow: hidden;
            .title{
                margin:48px 0 0 40px;
                > span{
                    font-size: 16px;
                    font-weight: 600;
                    > span{
                        font-size: 14px;
                        color:#999;
                        font-weight: normal;
                    }
                }
            }
            .information{
                width:100%;
                margin-top: 38px;
                li{
                    width:100%;
                    margin-bottom: 20px;
                    span{
                        width:120px;
                        text-align: right;
                        display: inline-block;
                        color:#989898;
                        font-size: 12px;
                    }
                    i{
                        font-style: normal;
                        color:red;
                    }
                    input{
                        margin-left: 20px;
                        background: #f4f4f4;
                        border:none;
                        border-radius: 5px;
                        width:400px;
                        height:42px;
                        box-sizing: border-box;
                        padding-left: 15px;
                    }
                }
                li:last-child{
                    display: flex;
                    span{
                        width:127px;
                        margin-top: 10px;
                    }
                    textarea{
                        width:680px;
                        height:110px;
                        background: #f4f4f4;
                        border:none;
                        margin-left: 20px;
                        border-radius: 5px;
                        resize: none;
                        box-sizing: border-box;
                        padding:13px 0 0 15px;
                    }
                }
                .hkdate{
                    display: flex;
                    flex-wrap: wrap;
                    span{
                        margin-top: 10px;
                        width:120px;
                    }
                    i{
                        margin-top: 8px;
                    }
                    div{
                        position: relative;
                        p{
                            position: absolute;
                            right:18px;
                            top:15px;
                            font-size: 11px;
                        }
                        > input{
                            background: #f4f4f4;
                            width:400px;
                            height:42px;
                            margin-left: 20px;
                            line-height: 42px;
                            box-sizing: border-box;
                            padding-left: 15px;
                            /*color:#999;*/
                        }
                        .choes{
                            position: absolute;
                            top:0;
                            left:20px;
                            width:400px;
                            height:42px;
                            opacity: 0;
                        }
                    }
                }
                .bankUser{
                    i{
                        margin-right: 17px;
                    }
                    select{
                        width:400px;
                        height:42px;
                        background: #f4f4f4;
                        border:none;
                        border-radius: 5px;
                        box-sizing: border-box;
                        padding-left: 10px;
                        padding-right: 10px;
                        appearance:none;
                        -moz-appearance:none;
                        -webkit-appearance:none;
                        background-image: url("http://ourjs.github.io/static/2015/arrow.png");
                        background-repeat: no-repeat;
                        background-position: 370px center;
                    }
                    select:invalid { color: gray; }
                }
            }
            button{
                margin-left: 147px;
                width:135px;
                height:47px;
                border-radius: 2px;
                background: #1e7fff;
                color:white;
                font-size: 14px;
                border:none;
                margin-bottom: 60px;
            }
        }
    }
    .warning,.warnings{
        color:red;
        padding-left: 150px;
        box-sizing: border-box;
        font-size: 9px;
        padding-top: 3px;
        opacity: 0;
    }
    .out{
        opacity: 1;
    }
</style>


